import React from "react";
import './Charge.css';

const Charge=(props)=>{
    const go_to_setting=()=>{
        props.changePage('setting')
    }
    return (
        <div className="Charge">
            <div className="blure">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>
                </div>
            </div>
            <div id="part1-Charge">
                <div className="cach-Charge">
                    <p></p>
                    <p><b style={{fontWeight:'600px',fontSize:'larger'}}>{props.data}</b></p>
                    <p></p>

                </div>
                <p className="tell-Charge">              ,       </p>
                <div className="increase-Charge">
                    <div className="submitPart-Charge">
                        <button> </button>
                    </div>
                    <div className="inputPart-Charge">
                        <input type="text" />

                    </div>
                    

                </div>
            </div>
            </div>

        </div>
    )
}

export default Charge;